@import "https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

nav {
  background-color: #684040;
  justify-content: space-between;
  align-items: center;
  padding: 10px 60px;
  display: flex;
}

nav .logo a {
  color: #fff;
  font-size: 35px;
  font-weight: 600;
  text-decoration: none;
}

nav ul {
  align-items: center;
  padding: 10px;
  list-style: none;
  display: flex;
}

nav li {
  margin-left: 7px;
}

nav li a {
  color: #fff;
  border-radius: 5px;
  padding: 8px 15px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

nav li a:hover {
  color: #000;
  background-color: #fff;
}

.banner {
  text-align: center;
  background-color: #0000;
  background-image: linear-gradient(#00000048, #0000004f), url("pantanal.20d10502.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.banner2 {
  text-align: center;
  background-color: #0000;
  background-image: linear-gradient(#00000048, #0000004f), url("bg-2.c4fa8129.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.texto .titulo {
  color: #fff;
  font-size: 60px;
  font-weight: 600;
}

.texto .sub_titulo {
  color: #fff;
  font-size: 58px;
  font-weight: 600;
}

.texto .botoes {
  margin-top: 30px;
}

.texto .botoes button {
  cursor: pointer;
  border: 0;
  border-radius: 5px;
  outline: none;
  width: 170px;
  height: 55px;
  margin: 0 10px;
  padding: 0 10px;
  font-size: 20px;
  font-weight: 500;
  transition: all .3s;
}

.texto .botoes button.n-overlay {
  color: #fff;
  background: none;
  border: 2px solid #fff;
}

.texto .botoes button.n-overlay:hover {
  color: #000;
  background-color: #fff;
}

.texto .botoes button.overlay {
  color: #000;
  background: #fff;
  animation: .5s ease-in-out infinite both vibrar;
}

@keyframes vibrar {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.text-paragrafo {
  color: #fff;
  font-size: 6px;
}
